import React, { useState } from 'react';
import { Card, CardContent } from './ui/card';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Textarea } from './ui/textarea';
import { Label } from './ui/label';
import { Phone, Mail, MessageCircle, Send } from 'lucide-react';
import { toast } from 'sonner';
import { motion } from 'framer-motion';

const ContactSection = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    telegram: '',
    business: '',
    request: '',
    instagram: '',
    service: ''
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    
    try {
      //  FormData    Google Forms
      const googleFormData = new FormData();
      
      //  entry ID   Google 
      googleFormData.append('entry.2047965147', formData.name); //  '
      googleFormData.append('entry.2067534945', formData.phone); //   '  
      googleFormData.append('entry.1942353565', formData.telegram); // 
      googleFormData.append('entry.703946920', formData.business); //   
      googleFormData.append('entry.1089921804', formData.request); //  
      googleFormData.append('entry.1033157831', formData.instagram); //   Instagram
      googleFormData.append('entry.1471033604', formData.service); //    
      
      //    Google 
      await fetch('https://docs.google.com/forms/d/e/1FAIpQLScPTPBVvl4aMeZ2DQ5u921kfvqMwzTwSvfXzv8df-bWtB23XQ/formResponse', {
        method: 'POST',
        mode: 'no-cors',
        body: googleFormData
      });
      
      toast.success(' !  \'    15  ');
      
      //  
      setFormData({
        name: '',
        phone: '',
        telegram: '',
        business: '',
        request: '',
        instagram: '',
        service: ''
      });
      
    } catch (error) {
      console.error(' :', error);
      toast.error('  .     \'   .');
    } finally {
      setIsLoading(false);
    }
  };

  const contactMethods = [
    {
      icon: Phone,
      title: '',
      value: '+380 (63) 712-38-14',
      action: '',
      color: 'text-green-600',
      bgColor: 'bg-green-100'
    },
    {
      icon: Mail,
      title: 'Email',
      value: 'arigamidigital@gmail.com',
      action: ' ',
      color: 'text-blue-600',
      bgColor: 'bg-blue-100'
    },
    {
      icon: MessageCircle,
      title: 'WhatsApp',
      value: '+380637123814',
      action: '  WhatsApp',
      color: 'text-green-600',
      bgColor: 'bg-green-100'
    }
  ];

  return (
    <section id="contact" className="py-20 bg-white">
      <div className="container mx-auto px-6">
        <div className="max-w-6xl mx-auto">
          {/* Header */}
          <motion.div 
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="text-center mb-16"
          >
            <h2 className="text-4xl lg:text-5xl font-display font-bold text-gray-900 mb-6">
               ?
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
                  ,       
            </p>
          </motion.div>

          <div className="grid lg:grid-cols-2 gap-12">
            {/* Contact Form */}
            <motion.div
              initial={{ opacity: 0, x: -50 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.8 }}
            >
              <Card className="border-0 shadow-xl">
              <CardContent className="p-8">
                <h3 className="text-2xl font-semibold text-gray-900 mb-6">
                   
                </h3>
                
                <form onSubmit={handleSubmit} className="space-y-6">
                  <div className="space-y-2">
                    <Label htmlFor="name"> ' *</Label>
                    <Input
                      id="name"
                      name="name"
                      value={formData.name}
                      onChange={handleInputChange}
                      placeholder=" '"
                      required
                      className="h-12"
                      disabled={isLoading}
                    />
                  </div>

                  <div className="grid md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="phone">  ' *</Label>
                      <Input
                        id="phone"
                        name="phone"
                        type="tel"
                        value={formData.phone}
                        onChange={handleInputChange}
                        placeholder="+380 (63) 712-38-14"
                        required
                        className="h-12"
                        disabled={isLoading}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="telegram"></Label>
                      <Input
                        id="telegram"
                        name="telegram"
                        value={formData.telegram}
                        onChange={handleInputChange}
                        placeholder="@your_username"
                        className="h-12"
                        disabled={isLoading}
                      />
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="business">  ? (   ?) *</Label>
                    <Input
                      id="business"
                      name="business"
                      value={formData.business}
                      onChange={handleInputChange}
                      placeholder="  ..."
                      required
                      className="h-12"
                      disabled={isLoading}
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="instagram">  Instagram</Label>
                    <Input
                      id="instagram"
                      name="instagram"
                      value={formData.instagram}
                      onChange={handleInputChange}
                      placeholder="https://instagram.com/your_account"
                      className="h-12"
                      disabled={isLoading}
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="service">   ? *</Label>
                    <select
                      id="service"
                      name="service"
                      value={formData.service}
                      onChange={handleInputChange}
                      required
                      disabled={isLoading}
                      className="w-full h-12 px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors"
                    >
                      <option value=""> </option>
                      <option value=" "> </option>
                      <option value="Google Ads ()">Google Ads ()</option>
                      <option value="SMM">SMM</option>
                      <option value=" + SMM"> + SMM</option>
                      <option value=" + Google"> + Google</option>
                      <option value=" 3  1"> 3  1</option>
                      <option value="//">//</option>
                      <option value=" /"> /</option>
                      <option value="   ">   </option>
                    </select>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="request">   ?</Label>
                    <Textarea
                      id="request"
                      name="request"
                      value={formData.request}
                      onChange={handleInputChange}
                      placeholder="    ..."
                      rows={4}
                      className="resize-none"
                      disabled={isLoading}
                    />
                  </div>

                  <Button 
                    type="submit" 
                    size="lg" 
                    disabled={isLoading}
                    className="w-full bg-blue-600 hover:bg-blue-700 text-white h-12 rounded-lg font-medium group"
                  >
                    {isLoading ? '...' : ' '}
                    {!isLoading && <Send className="ml-2 h-4 w-4 group-hover:translate-x-1 transition-transform" />}
                  </Button>
                </form>
              </CardContent>
            </Card>
            </motion.div>

            {/* Contact Info & CTA */}
            <motion.div 
              initial={{ opacity: 0, x: 50 }}
              whileInView={{ opacity: 1, x: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.8, delay: 0.2 }}
              className="space-y-8"
            >
              {/* Contact Methods */}
              <div className="space-y-6">
                <h3 className="text-2xl font-semibold text-gray-900">
                   '  
                </h3>
                
                {contactMethods.map((method, index) => {
                  const IconComponent = method.icon;
                  return (
                    <Card key={index} className="border-0 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 cursor-pointer">
                      <CardContent className="p-6">
                        <div className="flex items-center space-x-4">
                          <div className={`w-12 h-12 ${method.bgColor} rounded-full flex items-center justify-center`}>
                            <IconComponent className={`h-6 w-6 ${method.color}`} />
                          </div>
                          <div className="flex-1">
                            <div className="text-sm text-gray-500">{method.title}</div>
                            <div className="font-medium text-gray-900">{method.value}</div>
                          </div>
                          <div className={`text-sm ${method.color} font-medium`}>
                            {method.action}
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  );
                })}
              </div>

              {/* Quick CTA */}
              <Card className="bg-gradient-to-br from-blue-500 to-blue-600 border-0 text-white">
                <CardContent className="p-8 text-center">
                  <h4 className="text-xl font-semibold mb-4">
                      ?
                  </h4>
                  <p className="mb-6 opacity-90">
                            
                  </p>
                  <Button 
                    size="lg" 
                    variant="secondary"
                    onClick={() => window.open('tel:+380637123814', '_self')}
                    className="bg-white text-blue-600 hover:bg-gray-100 px-8 py-3 rounded-full font-medium"
                  >
                     
                  </Button>
                </CardContent>
              </Card>
            </motion.div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactSection;